






































































import Vue from 'vue'
import app from "../../App.vue"
import title from "../../utils/title.js"
import "../../static/css/animate.css";
import headerNav from "../../components/headerNav.vue"
let col1H = 0;
let col2H = 0;
export default {
	data() {
		return {
			data: {},
			page: 1,
			height: '',
			iphonex: false,
			scrollH: 0,
			imgWidth: 0,
			loadingCount: 0,
			images: [],
			col1: [],
			col2: [],
			// 自定义导航参数
			nvabarData: {
				showCapsule: 0,
				title: '食典录',
			},
			pullDown: false,
			offset: 0,
			limit: 20,
			is_weapp: 1,
			loading: false,
			title: "",
			isGoTop: false,
			imageObj: null,
			col1H: 0,
			col2H: 0
		}
	},
	onPullDownRefresh: function() {
		var that = this;
		that.pullDown = true,
			wx.vibrateShort()
		wx.showNavigationBarLoading() //在标题栏中显示加载

		setTimeout(function() {
			wx.hideNavigationBarLoading() //完成停止加载
			wx.stopPullDownRefresh() //停止下拉刷新
			that.pullDown = false,
				that.images = [],
				that.col1 = [],
				that.col2 = []
			that.loadImages()

		}, 1500);
	},
	onShow() {
		console.log(app.globalData)
	},
	onLoad() {
		wx.getSystemInfo({
			success: (res) => {
				let ww = res.windowWidth,
					wh = res.windowHeight;
				this.imgWidth = ww * 0.48;
				this.scrollH = wh;
				this.title = title[Math.floor(Math.random() * title.length)];
				this.height = app.globalData.height * 2 + 20;
				this.loadImages();
			}
		})
	},
	onReachBottom() {
		this.onscrollBotm()
	},
	onPageScroll: function(e) {
		if (e.scrollTop > 500) {
			this.isGoTop = true
		} else if (e.scrollTop < 10) {
			this.isGoTop = false
		}
	},
	methods: {
		// 点击置顶
		goTop: function() {
			// this.isGoTop = false;
			wx.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
		},
		onError(e) {
			console.log(e, 145)
		},

		onImageLoad: function(e) {
			let imageId = e.currentTarget.id;
			console.log(imageId)
			let oImgW = e.detail.width; //图片原始宽度
			let oImgH = e.detail.height; //图片原始高度
			let imgWidth = this.imgWidth; //图片设置的宽度
			let scale = imgWidth / oImgW; /*比例计算*/
			let imgHeight = oImgH * scale; //自适应高度
			let images = this.images;
			let imageObj = null;
			for (let i = 0; i < images.length; i++) {
				let img = images[i];
				// console.log(img.recipe.id, imageId, 121)
				if (img.recipe.id == imageId) {
					imageObj = img;
					break;
				}
			}
			imageObj.height = imgHeight;
			let loadingCount = this.loadingCount - 1;

			if (col1H <= col2H) {
				col1H += imgHeight;
				this.col1.push(imageObj);
				console.log(imgHeight, 143)
			} else {
				col2H += imgHeight;
				this.col2.push(imageObj);
				console.log(imgHeight, 147)
			}
			console.log(this.col2, 148)
			this.loadingCount = loadingCount;
			if (!loadingCount) {
				this.images = [];
			}
		},
		loadImages: function() {
			let that = this,
				images = [],
				param = new Object();
			param.offset = that.offset,
				param.limit = that.limit,
				param.is_weapp = that.is_weapp;
			that.off_on = true;
			this.$Api.getHomeList(param).then((res) => {
				console.log(res)
				if (res.data.content.recipes.length > 0) {
					for (var i = 0; i < res.data.content.recipes.length; i++) {
						images.push(res.data.content.recipes[i])
					}
					let baseId = "img-" + (+new Date());
					for (let i = 0; i < images.length; i++) {
						images[i].recipe.author.id = baseId + "-" + i;
					}
					that.off_on = false
					that.loadingCount = images.length
					that.images = images
					that.page = that.page
				} else {
					that.off_on = true
				}
			})
		},
		onscrollBotm() {
			let that = this;
			if (!that.off_on) {
				that.offset = that.offset += 20,
					that.loadImages()
			}
		},
		goDetaile(id) {
			uni.navigateTo({
				url: '../cateDetaile/cateDetaile?id=' + id
			});
		},

	},
	components: {
		headerNav
	},
	onShareAppMessage: function() {
		var that = this;
		return {
			title: "今天想吃点什么？",
			path: '/pages/index/index',
		}
	},
}
